.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  padding-top: 5rem;
  z-index: 2;
}

.modal-body {
  padding: 2rem;
  width: 400px;
  border-radius: 5px;
  background: #fff;
  height: 200px;
}

.btn {
  padding: 0.5rem 1rem;
  background-color: #fff;
  border-radius: 6px;
  border: 1px solid #ccc;
}
